import React from "react";
import { useNavigate } from "react-router-dom";
import "../assets/css/home.scss";
import coverHomePng from "../assets/images/coverHome.png";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import linePng from "../assets/images/LineBtn.png";
import logoMain from "../assets/images/logoBlue.svg";
import Team from "../components/Team";
import ServiceCard from "../components/ServiceCard";

const Home = () => {
  var settings = {
    infinite: true,
    speed: 100,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
    arrows: false,
    dots: false,
  };
  const navigate = useNavigate();
  return (
    <>
      <div className="home">
        <div className="home__cover__content">
          <h1 className="home__cover__content__title">de fendo vkil brosu</h1>
          <p className="home__cover__content__intro">
            Btn nv hquqi xidmtlr gstrn konsaltinq irktidir
          </p>
          <button
            onClick={() => navigate("/contact")}
            className="home__cover__content__button"
          >
            biziml laq
          </button>
        </div>
        <Slider {...settings} className="home__cover">
          <div>
            <img src={coverHomePng} alt="" />
          </div>{" "}
          <div>
            <img src={coverHomePng} alt="" />
          </div>
        </Slider>

        <div className="home__container custom-container">
          <div className="home__container__about__row row">
            <div className="home__container__about__row__left  col-lg-9 col-8">
              <h3 className="home__container__about__row__title ">
                Haqqmzda
              </h3>
              <p className="home__container__about__row__info">
                Azrbaycan Respublikasnn Vkillr Kollegiyasnn sas vzifsi
                hr bir xsin hquq v azadlqlarnn, qanunla qorunan
                mnafelrinin mdafi edilmsindn, onlara pekar, yksk
                keyfiyytli, vicdanl hquqi yardm gstrilmsini tmin
                etmkdn, vkillik pesinin nfuzunu yksltmkdn ibartdir.
                Azrbaycan Respublikas Vkillr Kollegiyasnn tkili v
                faliyyti "Vkillr v vkillik faliyyti haqqnda" Azrbaycan
                Respublikasnn Qanunu v ona mvafiq olaraq qbul edilmi
                Vkillr Kollegiyasnn Nizamnamsi il myyn edilir
              </p>
              <button className="home__container__about__row__button">
                <span className="home__container__about__row__button__span">
                  <img
                    className="home__container__about__row__button__span__img"
                    src={linePng}
                    alt=""
                  />
                </span>
                daha etrafli
              </button>
            </div>
            <div className="home__container__about__row__logo col-lg-3 col-4">
              <img src={logoMain} alt="" />
            </div>
          </div>
          <Team />
        </div>
      </div>
      <main className="p-0">
        <div className="custom-container">
          <div className="row">
            <ServiceCard />
          </div>
        </div>
      </main>
    </>
  );
};

export default Home;
